<template>
  <div class="page" @click="goIndex1">
    <div class="bg"></div>
   <div class="mask"></div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  name: "Index",
  data() {
    return {
        items: 20
    };
  },
  computed: {
    
  },
  mounted() {

  },
  methods: {
    goIndex1(){
      this.$router.push('Index1')
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
* {
  padding: 0;
  margin: 0;
}
html,
body {
  height: 100vh;
  width: 100vw;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Open Sans","PingFang SC","Microsoft YaHei","Helvetica Neue","Hiragino Sans GB","WenQuanYi Micro Hei",Arial,sans-serif;
}
@keyframes move {
 0% {
  background-position: 0 0;
 }
 50% {
  background-position: 100% 0;
 }
}
			.bg {
  background: url(https://sp-webfront.skypixel.com/skypixel/v2/public/website/assets/1535027674204-f6eca6369ec03e70262b58b0e25cda7b.jpg);
  background-size: cover;
  position: fixed;
  top: -20px;
  left: -20px;
  right: -20px;
  bottom: -20px;
  filter: blur(15px);
  z-index: -1;
}

.mask {
  width: 800px;
  height: 700px;
  animation: move 40s infinite;
  background-image: url(../images/11.png);
  background-size: contain;
  mask: url(../images/no-record.png) no-repeat;
  mask-size: contain;
}
</style>



