import Vue from 'vue'
import Vuex from 'vuex'
// import state from './state'
// import actions from './actions'
// import mutations from './mutations'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        count: 0,
        name:'cai'
    },
    mutations: {
        increment (state) {
            state.count++
        },
        changeName (state,name) {
            state.name=name;
        }
    },
    actions:{
        increment ({commit}) {
            commit('increment')
        },
        changeName({commit},name){
            commit('changeName',name)
        }
    }
})


